h1 {
    color:rgb(255, 234, 209);
    font-family:'Courier New', Courier, monospace;
}

.computerTable {
    background-image: url(https://i.imgur.com/NkIaoiA.png);

    background-repeat: no-repeat;
    background-size: contain;
}

#computerTable {
    position: relative;
    height: 360px;
    width: 400px;
}

.computerSpace {
    font-family: Arial, Helvetica, sans-serif;
    color:rgb(0, 0, 0);
    height: 69px;
    width: 80px;
    border: transparent;
    background: transparent;
    vertical-align: bottom;
    cursor: pointer;
}

.computerSpaceRow {
    font-family: Arial, Helvetica, sans-serif;
    color:rgb(0, 0, 0);
    height: 69px;
    width: 400px;
    border: transparent;
    background: transparent;
    vertical-align: bottom;
    cursor: pointer;
}

.computerSpacePartRow {
    font-family: Arial, Helvetica, sans-serif;
    color:rgb(0, 0, 0);
    height: 69px;
    width: 320px;
    border: transparent;
    background: transparent;
    vertical-align: bottom;
    cursor: pointer;
}

#computerSpaceA1 {
    position: absolute;
    top: 0;
    left: 0;
}

#computerSpaceB1 {
    position: absolute;
    top: 20%;
    left: 0;
}

#computerSpaceC1 {
    position: absolute;
    top: 40%;
    left: 0;
}

#computerSpaceD1 {
    position: absolute;
    top: 60%;
    left: 0;
}

#computerSpaceD5 {
    position: absolute;
    top: 60%;
    left: 80%;
}

#computerSpaceE1 {
    position: absolute;
    top: 80%;
    left: 0;
}

#computerSpaceE5 {
    position: absolute;
    top: 80%;
    left: 80%;
}